import React from "react";
import Navbar from "./../components/Navbar";
import { makeStyles } from '@material-ui/core/styles';
import Container from "@material-ui/core/Container";
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import TablePagination from '@material-ui/core/TablePagination';
//import Tablex from '@material-ui/core/Tablex'
import Paper from '@material-ui/core/Paper'
import Footer from "./../components/Footer";

import data from "./../components/demoLiqData.json"

const useStyles = makeStyles({
  root: {
    width: '100%',
  },
  container: {
    maxHeight: 440,
  },
});
function l(url, text) {
    return ( <a href={url}>{text}</a> )
}
function addr_escan(addr) {
    return 'https://etherscan.io/address/' + addr
}
function tx_escan(tx) {
    return 'https://etherscan.io/tx/' + tx
}
const liqStyles = makeStyles({
  created: { color: 'black' },
  withdrawn: { color: 'grey' },
  disputed: { color: 'red' },
  settled: { color: 'pink' },
})
function fancyLiqType(type) {
    const liqClasses = liqStyles()
    let ret = ''
    switch (type) {
        case 'LiquidationCreatedEvent':
            ret = (<span className={liqClasses.created}>Created</span>)
            break
        case 'LiquidationWithdrawnEvent':
            ret = (<span className={liqClasses.withdrawn}>Withdrawn</span>)
            break
        case 'LiquidationDisputedEvent':
            ret = (<span className={liqClasses.disputed}>Disputed</span>)
            break
        case 'LiquidationDisputeSettledEvent':
            ret = (<span className={liqClasses.settled}>Dispute Settled</span>)
            break
        default:
            throw Error('bad type: ' + type)
    }
    return ret
}

function liqRowObj(ev) {
    const liq = ev.liquidation
    const calcLiqPrice = liq.tokensLiquidated / liq.liquidatedCollateral

    return {
        lid: (<span><a name={'id-' + ev.id}></a><a href={'#id-' + ev.id}>LiD</a></span>),
        date: (new Date(parseInt(ev.timestamp)*1000)).toISOString().replace('T', ' ').split('.')[0],
        tx: l(tx_escan(ev.tx_hash), 'TX'),
        liqType: fancyLiqType(ev.__typename),
        disputeStatus: liq.status == 'PreDispute' ? '' : liq.status,
        sponsor: l(addr_escan(ev.sponsor), (<img src={'https://robohash.org/' + ev.sponsor + '.png?size=100x100'} />)),
        liquidator: l(addr_escan(liq.liquidator.address), (<img src={'https://robohash.org/' + liq.liquidator.address + '.png?size=100x100'} />)),
        collateral: [liq.liquidatedCollateral, liq.position.collateralToken.name, '@' + calcLiqPrice].map(p => (<p>{p}</p>)),
        token: [liq.tokensLiquidated, liq.position.syntheticToken.name].map(p => (<p>{p}</p>))
    }
}

function LiquidationsPage(props) {
  const classes = useStyles();
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(10);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(+event.target.value);
    setPage(0);
  };
  const rows = data.data.liquidationEvents.map(ev => liqRowObj(ev))
  return (
    <>
      <Navbar
        color="default"
        logo="https://robohash.org/0xr3kt1355.png?size=100x100"
      />
      <Container>
        <Paper className={classes.root}>
          <TableContainer component={Paper}>
          <Table stickyHeader className='table' aria-label="sticky table">
            <TableHead>
              <TableRow>
                <TableCell>ID</TableCell>
                <TableCell align="right">Date</TableCell>
                <TableCell align="right">TX</TableCell>
                <TableCell align="right">Event Type</TableCell>
                <TableCell align="right">Dispute</TableCell>
                <TableCell align="right">Sponsor</TableCell>
                <TableCell align="right">Liquidator</TableCell>
                <TableCell align="right">Collateral</TableCell>
                <TableCell align="right">Token</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {rows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => (
                <TableRow key={row.name}>
                  <TableCell component="th" scope="row">
                    {row.lid}
                  </TableCell>
                  <TableCell align="right">{row.date}</TableCell>
                  <TableCell align="right">{row.tx}</TableCell>
                  <TableCell align="right">{row.liqType}</TableCell>
                  <TableCell align="right">{row.disputeStatus}</TableCell>
                  <TableCell align="right">{row.sponsor}</TableCell>
                  <TableCell align="right">{row.liquidator}</TableCell>
                  <TableCell align="right">{row.collateral}</TableCell>
                  <TableCell align="right">{row.token}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
          </TableContainer>
          <TablePagination
        rowsPerPageOptions={[10, 25, 100]}
        component="div"
        count={rows.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onChangePage={handleChangePage}
        onChangeRowsPerPage={handleChangeRowsPerPage}
      />
        </Paper>
      </Container>
      <Footer
        bgColor="default"
        size="medium"
        bgImage=""
        bgImageOpacity={1}
        description="UMA Liquidations & More"
        copyright="© 2021 UMANIAC"
        logo="https://robohash.org/0xr3kt1355.png?size=100x100"
        sticky={true}
      />
    </>
  );
}

export default LiquidationsPage;
